}

.proj-links svg, .swe-card svg {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.swe-card {
    background-color: #d2dce9;
    min-height: 75vh;
    max-width: 80vw;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin: 0 35px 70px 35px;
    padding: 0px !important;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}


.p5 {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    max-width: 250px !important;

}

.design {
    display: inline-block;
    position:relative;
    max-width: 250px;
    max-height: 357px;
    margin: 10px 10px 40px 10px;
}

.design-desc {
    background-color: rgba(0, 0, 0, 0.6);
    height: 100%;
    width: 100%;
    opacity: 0;
    top: 0;
    left: 0;
    color: white;
    position: absolute;
    display: inline-block;
    padding: 5vw 1.5vw 0 1.5vw;
    text-align: center !important;
    transition: opacity .5s;
}
.design-desc:hover {
    opacity: 1;
    transition: opacity .5s;
}

.contact {
    display: flex;
    justify-content: center;
}

.contact a {
    color: #6179e4;
    padding: 40px 20px;
}

.animate {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

/*=== FADE IN LEFT ===*/
@-webkit-keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-10%, 0, 0);
    transform: translate3d(-10%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-10%, 0, 0);
    transform: translate3d(-10%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.fadeInLeft {
  -webkit-animation-name: fadeInLeft;
  animation-name: fadeInLeft;
}

.fade-delay {
    -webkit-animation-delay: 0.25s;
    animation-delay: 0.25s;
}


.floating { 
    -webkit-animation-name: floating; 
            animation-name: floating;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}
 
@-webkit-keyframes floating {
    0% { -webkit-transform: translate(0,  0px); transform: translate(0,  0px); }
    50%  { -webkit-transform: translate(0, 10px); transform: translate(0, 10px); }
}
 
@keyframes floating {
    0% { -webkit-transform: translate(0,  0px); transform: translate(0,  0px); }
    50%  { -webkit-transform: translate(0, 10px); transform: translate(0, 10px); }
}

.enlarge {
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s; /* Animation */
}
  
.project-card1:hover, .project-card2:hover, .swe-card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
